  async initialization() {
    try {
      const response = await fetch('http://ememory.sc2bomo9230.universe.wf/db_management/memories.php?ID=2');
      if (response.ok) {
        const data = await response.json();
        this.setState({ memories: [] });
        this.setState({ memories: data });
      } else {
        throw new Error('Network response was not ok.');
      }
    } catch (error) {
      //   console.error('Error:', error);
    }
  }

  componentDidMount() {
    this.initialization();
  }

  render() {
    return (
      <MUI.Grid container className='Memories'
        spacing={1}
        p={1}
        pr={0}
        pb={2}
        sx={{
          width: '100%',
        }}
      >
        {this.props.children}
        {this.state.memories.map((memory) => (
          <Memory
            key={memory.ID}
            ID={memory.ID}
            Name={memory.Name}
            img={memory.img}
            Description={memory.Description}
            Member={memory.Member}
            Family={memory.Family}
            Family2={memory.Family2}
          />
        ))}
      </MUI.Grid>
    );
  }
