import React, { Component } from 'react';
// import './Header.css';
import * as MUI from '@mui/material';
import * as MaterialIcons from '@mui/icons-material';
class Header extends Component {
    constructor(props) {
        super(props);
        this.props = {
            text: "Memorico"
        }
    }
    scrollToElementRef = React.createRef();
    handleScrollToClick = () => {
        this.scrollToElementRef.current.scrollIntoView({ behavior: 'smooth' });
    };
    render() {
        // console.log(this.props);
        return (
            <>
                <MUI.AppBar className="pointer" position="relative" ref={this.scrollToElementRef} sx={{
                    bgcolor: 'primary.main',
                    backdropFilter: 'blur(10px)',
                    width: '100%',
                    userSelect: 'none',
                    opacity: '0%'
                }}>
                    <MUI.Toolbar onClick={this.handleScrollToClick}>
                        {/* <MUI.Avatar alt="Memorico" src="../icon.jpg" sx={{ margin: 1, height: 56, width: 56 }}>M</MUI.Avatar> */}
                        <MUI.Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                            {this.props.text}
                        </MUI.Typography>
                        {this.props.children}
                    </MUI.Toolbar>
                </MUI.AppBar>
                <MUI.AppBar className="pointer" position="fixed" sx={{
                    bgcolor: 'primary.main',
                    backdropFilter: 'blur(10px)',
                    width: '100%',
                    userSelect: 'none',
                }}>
                    <MUI.Toolbar onClick={this.handleScrollToClick}>
                        {/* <MUI.Avatar alt="Memorico" src="../icon.jpg" sx={{ margin: 1, height: 56, width: 56 }}>M</MUI.Avatar> */}
                        <MUI.Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                            {this.props.text}
                        </MUI.Typography>
                        {this.props.children}
                    </MUI.Toolbar>
                </MUI.AppBar>
            </>
        )
    }
}

export default Header;