import Header from '../components/Header';
// import './Login.css';
import * as MUI from '@mui/material';
import * as MaterialIcons from '@mui/icons-material';
import React, { Component, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';

function Navigation() {
  const navigate = useNavigate();
  const goToLanding = () => {
    navigate('/');
  };
  return (
    <Header text="Login" onClick={goToLanding} />
  );
}

function Main() {
  const [data, setData] = useState({
    username: '',
    password: '',
    state: false
  })
  const navigate = useNavigate();
  const handleLogin = event => {
    console.log(` ${data.username} - ${data.password}`);
    event.preventDefault();
    navigate('/dashboard')
  };

  return (
    <form onSubmit={handleLogin}>
      <MUI.Paper elevation={24} sx={{
        display: 'flex',
        flexDirection: 'column',
        gap: { sm: 0, md: 2, lg: 4 },
        borderRadius: 3,
        m: { sm: 0, md: 2, lg: 8 },
        p: { sm: 0, md: 2, lg: 4 }
      }}>
        <MUI.TextField
          required
          sx={{ mx: 2 }}
          label="Username:"
          value={data.username}
          onChange={function (event) {
            setData({
              username: event.target.value,
              password: data.password,
              state: data.state
            })
          }}
          InputProps={{
            startAdornment: (
              <MUI.InputAdornment position="start">
                <MaterialIcons.AccountCircle />
              </MUI.InputAdornment>
            ),
          }}
          variant="filled"
        />
        <MUI.TextField
          required
          sx={{ mx: 2 }}
          label="Password:"
          value={data.password}
          onChange={function (event) {
            setData({
              username: data.username,
              password: event.target.value,
              state: data.state
            })
          }}
          type={data.state ? 'text' : 'password'}
          InputProps={{
            endAdornment: (
              <MUI.InputAdornment position="end">
                <MUI.IconButton
                  onClick={function () {
                    setData({
                      username: data.username,
                      password: data.password,
                      state: !(data.state)
                    })
                  }}
                  edge="end"
                >
                  {data.state ? <MaterialIcons.VisibilityOff /> : <MaterialIcons.Visibility />}
                </MUI.IconButton>
              </MUI.InputAdornment>
            ),
          }}
          variant="filled"
        />
        <MUI.Button
          // onClick={() => {
          //   alert(` ${data.username} - ${data.password}`)
          // }}
          // href="/dashboard"
          variant="contained"
          size="large"
          type='submit'
          sx={{
            borderRadius: 5,
            bgcolor: 'primary.main',
            "&:hover": {
              bgcolor: 'primary.light'
            }
          }}
        >
          Login
        </MUI.Button>
      </MUI.Paper>
    </form>
  );
}

class Login extends Component {
  scrollToElementRef = React.createRef();
  handleScrollToClick = () => {
    this.scrollToElementRef.current.scrollIntoView({ behavior: 'smooth' });
  };
  componentDidMount() {
    const cookieExists = Cookies.get('user');
    if (cookieExists) this.props.navigate('/dashboard');
  };
  render() {
    return (
      <div className="Login">
        <Navigation />
        <Main />
      </div>
    )
  }
}

export default Login;
